/*------------------------------------*\
  #CHECKOUT HEADER
\*------------------------------------*/

.wfc-checkout-header {
	z-index: z( 'sticky-element' );
	position: fixed;
	top: 0;
	left: 0;

	display: flex;
	align-items: center;
	width: 100%;
	min-height: $checkout-header-height;
	background-color: $checkout-header-background-color;
	box-shadow: $checkout-header-box-shadow;
	
	@media (#{$bp-larger-than-desktop}) {
		position: static;
		min-height: $checkout-header-height--large-screen;
		box-shadow: none;
		background-color: transparent;
	}
}
// With admin bar
.admin-bar .wfc-checkout-header {
	top: 46px; // Uses pixels to match WordPress admin bar

	@media screen and (min-width: 783px) { // Uses absolute values for breakpoint to match WordPress admin bar
		top: 32px; // Uses pixels to match WordPress admin bar
	}
}

.wfc-checkout-header + .wfc-checkout-notices,
.wfc-checkout-header + form.woocommerce-checkout {
	@media (#{$bp-smaller-than-desktop}) {
		padding-top: #{ $checkout-header-height + $global-spacing-small };
	}
}

.wfc-checkout-header__inner {
	position: relative;
	width: 100%;
	
	// max-width
	max-width: $container-width-mobile;
	@media (#{$bp-larger-than-tablet}) { max-width: $container-width-tablet; }
	@media (#{$bp-larger-than-desktop}) { max-width: none; }

	// padding
	padding: 0 $container-spacing-mobile;
	@media (#{$bp-larger-than-tablet}) { padding: 0 $container-spacing-tablet; }
	@media (#{$bp-larger-than-desktop}) { padding: 0; }
}





.wfc-checkout-header .wfc-checkout__branding {
	width: 50%;
	line-height: 1;
}

.wfc-checkout-header .wfc-checkout__title.screen-reader-text {
	clip: rect( 1px, 1px, 1px, 1px );
	position: absolute !important; // Needed to make sure other style rules won't affect accessibility
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.wfc-checkout-header .wfc-checkout__site-name {
	display: inline-block;
	margin: $global-spacing-extra-small 0;
	padding: 0;
	font-size: $font-size;
	font-weight: bold;
}

.wfc-checkout-header .custom-logo-link {
	display: inline-block;
	margin: remsize( .2rem ) 0 0;
}
.wfc-checkout-header .custom-logo {
	margin: #{ ( $checkout-header-height - $checkout-logo-height ) / 2 } 0;
	height: $checkout-logo-height;
	width: auto;

	@media (#{$bp-larger-than-desktop}) {
		margin: #{ ( $checkout-header-height--large-screen - $checkout-logo-height--large-screen ) / 2 } 0;
		height: $checkout-logo-height--large-screen;
	}
}





.wfc-checkout-header .wfc-checkout__cart-link-wrapper {
	position: absolute;
	top: 0;
	right: #{ $container-spacing-mobile - $global-spacing-extra-small };
	margin: 0;
	
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: $checkout-header-height;
	line-height: 1;

	@media (#{$bp-larger-than-mobile}) { right: #{ $container-spacing-mobile }; } // Larger mobile devices
	@media (#{$bp-larger-than-tablet}) { right: #{ $container-spacing-tablet }; }
	@media (#{$bp-larger-than-desktop}) { display: none; }
}
.wfc-checkout-header .wfc-checkout__cart-link {
	margin: $global-spacing-tiny 0;
	padding: $global-spacing-extra-small remsize( .8rem ) remsize( .8rem ) $global-spacing-extra-small;
	background-color: $white;
	border: solid remsize( .1rem ) $border-light-color;
	border-radius: $global-radius;
	box-shadow: $box-shadow-dark;
	font-size: $font-size-small;
	text-decoration: none;
	color: inherit;
	transition: all $global-animation-duration $global-animation-easing;

	&:active {
		box-shadow: none;
		transform: translateY( remsize( .1rem ) );
		color: inherit;
	}
}
.wfc-checkout-header .wfc-checkout__cart-link:before {
	@include icon-styles();
	@include icon( 'i-cart' );
	@include icon-size( $icon-size-small );
	
	margin-right: $global-spacing-extra-small;
}
.wfc-checkout-header .wfc-checkout__cart-link:after {
	@include icon-styles();
	@include icon( 'i-chevron-down' );
	@include icon-size( $icon-size-small );
}

.has-flyout--open-wfc-checkout-order-review .wfc-checkout-header .wfc-checkout__cart-link {
	background-color: $lighter-grey;
	box-shadow: none;
	transform: translateY( remsize( .1rem ) );
}
.has-flyout--open-wfc-checkout-order-review .wfc-checkout-header .wfc-checkout__cart-link:after {
	@include icon( 'i-chevron-up' );
}





.wfc-checkout-header .wfc-checkout__header-widgets {
	@media (#{$bp-larger-than-desktop}) {
		position: absolute;
		top: 50%;
		right: 0;
		transform: translateY( -50% );
		display: flex;
		justify-content: flex-end;
		margin: 0;
		width: 50%;
	}
}
