/*------------------------------------*\
  #CHECKOUT SHIPPING METHODS
\*------------------------------------*/

.shipping-method__package {
	margin: 0 0 $global-spacing-small;
}



.shipping-method__options {
	list-style-type: none;
	border: $shipping-method-border;
	border-radius: $shipping-method-border-radius;
	overflow: hidden;
}



#shipping_method li.shipping-method__option {
	display: block;
	margin: 0 0 $shipping-method-space-between-options;
}



// Hide default radio elements with opacity to allow for keyboard navigation
.shipping-method__option input[type="radio"] {
	position: absolute;
	display: block;
	width: 1px;
	height: 1px;
	opacity: 0;
}



.shipping-method__option .shipping-method__option-label {
	position: relative;
	display: block;
	margin: 0;
	padding: $shipping-method-padding-vertical #{ $shipping-method-padding-horizontal + $shipping-method-price-width } $shipping-method-padding-vertical #{ $global-spacing-extra-small + $shipping-method-radio-size + $shipping-method-padding-horizontal };
	width: 100%;
	border-bottom: $shipping-method-border;
	font-weight: $shipping-method-font-weight;
	cursor: pointer;
	transition: all $global-animation-duration-fast linear;
}
.shipping-method__option:last-child .shipping-method__option-label {
	border-bottom: none;
}

.shipping-method__option input[type="radio"]:checked + .shipping-method__option-label {
	background-color: $shipping-method-checked-background-color;
	color: $shipping-method-checked-color;
}



.shipping-method__option .shipping-method__option-label:before {
	box-sizing: border-box;
	content: '';
	position: absolute;
	top: 50%;
	transform: translateY( -50% );
	left: $global-spacing-extra-small;
	display: block;
	width: $shipping-method-radio-size;
	height: $shipping-method-radio-size;
	border: $shipping-method-radio-border;
	border-radius: 100%;
	transition: all $global-animation-duration-fast linear;
}

.shipping-method__option input[type="radio"]:checked + .shipping-method__option-label:before {
	border-color: $success-color;
	background-color: $success-color;
}

.shipping-method__option .shipping-method__option-label:hover {
	background-color: $shipping-method-checked-background-color;
}
.shipping-method__option .shipping-method__option-label:hover:before,
.shipping-method__option .shipping-method__option-label:focus:before {
	border-color: darken( $shipping-method-border-color, 15% );
}

.shipping-method__option input[type="radio"]:checked + .shipping-method__option-label:after {
	@include icon-styles();
	@include icon( 'i-check-small' );
	@include icon-size( $shipping-method-radio-size );

	position: absolute;
	top: 50%;
	transform: translateY( -50% );
	left: $global-spacing-extra-small;
	color: $white;
}



.shipping-method__option .shipping-method__option-price {
	position: absolute;
	top: 50%;
	transform: translateY( -50% );
	right: $shipping-method-padding-horizontal;
	display: block;
	line-height: 1.2;
	text-align: right;
}

.shipping-method__option .shipping-method__option-price .amount {
	display: block;
	font-weight: $shipping-method-price-font-weight;
}

.shipping-method__option .shipping-method__option-price .tax_label {
	display: block;
}
